import BeLogoFooter from '@/assets/be_logo_footer';
import app_store from '@/stores/app.store';
import { Component, h } from '@stencil/core';

@Component({
  tag: 'ir-footer',
  styleUrl: 'ir-footer.css',
  shadow: true,
})
export class IrFooter {
  private socials = {
    '006': 'whatsapp',
    '002': 'instagram',
    '001': 'facebook',
    '005': 'skype',
    '004': 'youtube',
    '003': 'twitter',
  };
  contactDialog: HTMLIrDialogElement;
  render() {
    return (
      <footer class="flex items-center  border-t px-4 py-2 lg:px-6">
        <ul class="mx-auto flex w-full max-w-6xl flex-col items-center  justify-between space-y-2 md:flex-row md:space-y-0">
          <li class="flex w-full items-center justify-center gap-1.5 md:w-fit md:justify-between">
            <p class="font-medium">{app_store.property?.name}</p>
            <span>-</span>
            <ir-button onButtonClick={() => this.contactDialog.openModal()} buttonStyles={{ padding: '0' }} variants="link" label="Contact"></ir-button>
            <span>-</span>
            <ir-privacy-policy label="Policy"></ir-privacy-policy>
          </li>
          <li class="flex items-center gap-4">
            {app_store.property?.social_media.map(media => {
              if (media.link === '') {
                return null;
              }
              const href = media.code === '006' ? `https://api.whatsapp.com/send/?phone=${media.link}` : media.link;
              return (
                <a target="_blank" href={href} title={media?.name}>
                  <ir-icons name={this.socials[media.code] as any}></ir-icons>
                </a>
              );
            })}
          </li>
          <li class="">
            <a href="https://info.igloorooms.com/" target="_blank" title="igloorooms cloud booking solutions for hotels">
              <BeLogoFooter width={120} height={60} />
              {/* <img src={'/assets/BE_logo_footer.png'} alt="igloorooms web booking engine" class="size-10 w-32 object-contain" /> */}
            </a>
          </li>
        </ul>
        <ir-dialog ref={el => (this.contactDialog = el)} style={{ '--ir-dialog-max-width': '25rem' }}>
          <div class="max-h-[83vh] overflow-y-auto p-4  text-[var(--gray-600,#475467)] md:p-6" slot="modal-title">
            <h1 class="mb-4 text-xl font-semibold text-[var(--gray-700,#344054)]">Contact information</h1>
            <div class="text-sm">
              <label>Phone:</label>
              <a slot="dialog-body" href={`tel:${app_store.property?.phone}`}>
                {app_store.property?.phone}
              </a>
            </div>
            {/* <div class="text-sm">
                    <label>Email</label>
                    <a slot="dialog-body" href={`tel:${app_store.property?.phone}`}>
                      {app_store.property?.}
                    </a>
                  </div> */}
          </div>
        </ir-dialog>
      </footer>
    );
  }
}
